import React, { Component } from 'react'
import Fade from 'react-reveal/Fade'

export default class RequestVisit extends Component {
  render() {
    return (
      <Fade bottom>
        {' '}
        <div className="cls-request-form-wrapper">
          <h3 className="cls-header3 mb-4">Request For Visit</h3>
          <form
            action="https://docs.google.com/forms/d/e/1FAIpQLScEdeB5csWgbFRm5C8qKgaRPEZYYL9ngBlghNoyA3wNjamGtA/formResponse"
            method="POST"
            target="_blank"
          >
            <div className="form-row mb-3">
              <div className="col">
                <input
                  type="text"
                  className="form-control"
                  placeholder="Full Name"
                  name="entry.956001330"
                  required
                />
              </div>
            </div>
            <div className="form-row mb-3">
              <div className="col">
                <input
                  type="email"
                  className="form-control"
                  placeholder="Email Address"
                  name="entry.620470980"
                  required
                />
              </div>
            </div>
            <div className="form-row mb-3">
              <div className="col">
                <input
                  type="number"
                  className="form-control"
                  placeholder="Phone Number"
                  name="entry.735190276"
                  required
                />
              </div>
            </div>
            <div className="form-row mb-3">
              <div className="col">
                <textarea
                  type="text"
                  className="form-control"
                  placeholder="Message"
                  rows="5"
                  name="entry.1444582786"
                  required
                />
              </div>
            </div>
            <button type="submit" className="btn btn-primary btn-block mb-2">
              Submit
            </button>
          </form>
        </div>
      </Fade>
    )
  }
}



