import React, { Component } from 'react'
import Fade from 'react-reveal/Fade'

export default class ContactForm extends Component {
  render() {
    return (
      <Fade bottom>
        <section id="sec-contact-form">
          <div className="container">
            <h3 className="cls-header3 mb-3">Contact Form</h3>
            <form
              action="https://docs.google.com/forms/d/e/1FAIpQLScEdeB5csWgbFRm5C8qKgaRPEZYYL9ngBlghNoyA3wNjamGtA/formResponse"
              method="POST"
              target="_blank"
            >
              <div className="form-row mb-3">
                <div className="col">
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Full Name"
                    name="entry.956001330"
                    required
                  />
                </div>
                <div className="col">
                  <input
                    type="email"
                    className="form-control"
                    placeholder="Email"
                    name="entry.620470980"
                    required
                  />
                </div>
                <div className="col">
                  <input
                    type="number"
                    className="form-control"
                    placeholder="Contact Number"
                    name="entry.735190276"
                    required
                  />
                </div>
              </div>
              <div className="form-row mb-3">
                <div className="col">
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Subject"
                    name="entry.380366669"
                    required
                  />
                </div>
              </div>
              <div className="form-row mb-3">
                <div className="col">
                  <textarea
                    type="text"
                    className="form-control"
                    placeholder="Message"
                    rows="5"
                    name="entry.1444582786"
                    required
                  />
                </div>
              </div>
              <button type="submit" className="btn btn-primary mb-2">
                Submit
              </button>
            </form>
          </div>
        </section>
      </Fade>
    )
  }
}



